@import url(https://fonts.googleapis.com/css2?family=Maven+Pro:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Kiwi+Maru&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Varela+Round&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Londrina+Solid&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Varela+Round&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



.App {
  text-align: center;
  margin: 0px 0px;
  background-color:black;
  background-image:
  radial-gradient(white, rgba(255,255,255,.2) 2px, transparent 40px),
  radial-gradient(white, rgba(255,255,255,.15) 1px, transparent 30px),
  radial-gradient(white, rgba(255,255,255,.1) 2px, transparent 40px),
  radial-gradient(blueviolet, rgba(255,255,255,.1) 2px, transparent 30px);
  background-size: 550px 550px, 350px 350px, 250px 250px, 150px 150px;
  background-position: 0 0, 40px 60px, 130px 270px, 70px 100px;
  min-height: 100%;
  min-width: 1024px;
  width: 100%;
  height: auto;
  position: absolute;
  top: 0;
  left: 0;
  /* bottom: -50px; */
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.catMain{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 20px;
  }
  
img {
  max-width: 400px;
  max-height: 400px;
  border-radius: 20px 20px;
  box-shadow: 0px 0px 10px blueviolet;
}

.homeDes {
  width: 375px;
  font-size: 20px;
  color: white;
  /* border: 4px solid blue; */
  border-radius: 20px 20px;

}
  
.catButton:hover{
  box-shadow: 0px 0px 10px gray;
  transform: scale(1.1);
  transition: ease-in-out .5s;
}

.catButton {
  position: relative;
  color: white;
  /* background: radial-gradient(ellipse at center, white 0%, orange 100%); */
  border-radius: 20px 20px;
  height: 50px;
  padding: 15px 32px;
  margin: 20px 20px;
  font-family: 'Maven Pro', sans-serif;
  font-size: 20px;
  background: url(https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcRqYa4NCAfv7JDy111ua-mwSjLsWgY-jKIBrw&usqp=CAU);
  background-size: 100%;
  background-position-y: 50%;
  background-position-x: 50%;
}


  

.header-main {
  display: flex;
  color:white;
  flex-direction: column;
  margin: 0px 20px;
  /* background-color:#66a3ff; */
  /* background:
  radial-gradient(circle at 100% 50%, transparent 20%, rgba(255,255,255,.3) 21%, rgba(255,255,255,.3) 34%, transparent 35%, transparent),
  radial-gradient(circle at 0% 50%, transparent 20%, rgba(255,255,255,.3) 21%, rgba(255,255,255,.3) 34%, transparent 35%, transparent) 0 -50px;
  background-color: orangered;
  background-size:75px 100px; */
  border-radius: 30px 30px;
  background: url(https://wallpaperaccess.com/full/876271.jpg);
  background-size: 38%;
  background-position-x: -12%;
  box-shadow: 0px 0px 10px blueviolet;
}

.cats {
  font-family: 'Kiwi Maru', serif;
  font-size: 50px;
  -webkit-text-stroke: 0.1px black;
}

.homeLink, .breedLink{
  display: block;
  font-family: 'Kiwi Maru', serif;
  color: white;
  text-decoration: none;
}

.homeLink:hover, .breedLink:hover{
  display: block;
  font-family: 'Kiwi Maru', serif;
  color: yellow;
  text-decoration: none;
  text-shadow: 0px 0px 10px lightblue;
}

.breed {
    text-align: center;
    font-family: 'Varela Round', sans-serif;
    color: white;
    display: block;
    font-size: 30px;
    height: 65px;
    width: 120px;
    text-shadow: 0px 0px 2px black;
    /* border: 2px solid; */
    -webkit-text-stroke: 0.1px black;
    
}

.breed:hover {
    text-shadow: 0px 0px 10px orange;
    color: yellow;
    transform: scale(1.2);
}

.catBreeds {
    font-size: 35px;
    font-family: 'Varela Round', sans-serif;
    color: white;
}

.breedId {
    text-decoration: none;
    margin: 50px 20px;
    display: block;
    height: 90px;
    width: 140px;
    padding: 0px 20px;
    /* border: 1px solid blue; */
    
}

.breed2 {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    /* border: 2px dashed; */
    /* background-color:black; */
    margin: 20px 20px;
    border-radius: 20px 20px;
    opacity: 0.8;
    /* background: url(https://i.imgur.com/AYTaKY9.jpg); */
    background: url(https://media.istockphoto.com/vectors/set-of-black-cat-icons-vector-id1124670239?k=6&m=1124670239&s=612x612&w=0&h=ryViT97NPXv2iM7CsuJRp4z2GKl3VwpT9N5nj4-bTP8=);
    background-size: 52.5%;
    background-position-x: -5%;
    /* background-attachment: fixed; */
    /* background-repeat: no-repeat; */
}


.breedName {
    display: flex;
    justify-content: center;
    text-align: center;
    font-family: 'Varela Round', sans-serif;
    font-size: 30px;
    color: white;
}

p {
    font-family: 'Varela Round', sans-serif;
    font-size: 20px;
    text-align: center;
    /* margin-top: -20px; */
    width: 600px;
    color: white;
}

.catImage{
    margin-top: 40px;
    box-shadow: 0px 0px 15px blueviolet;
}

.content {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.DesBox {
    width: 600px;
}

